header,
header .menu_bar .logo_mob {
    width: 100%;
    font:700 30px 'Open Sans';
}

header .menu_bar .sign_mob, header .menu_bar .logo_mob_text {
    display: none
}


header .logo_mob img{
    height: 50px;
    visibility: hidden;
}

.header_home{
    background:none;
}

.header_home .menu_nav{
    background: none;
}

.header_home .menu_nav ul li a{
    color:#fff!important;
    text-transform: uppercase;
}

#header header {
    padding: 10px 50px;
    box-sizing: border-box;
    position:absolute;
    z-index: 100;
    background: none

}

header .menu_bar {
    display: inline-block;
    max-width: 200px;
    vertical-align: middle;
    z-index: 1000
}

header .ctr_menu {
    display: none
}

header nav {
    z-index: 1000;
    max-width: 1000px;
    text-align: right;
    float: right
}

header nav ul {
    list-style: none;
    margin: 0!important;
    padding-left: 0px
}

header nav ul li {
    display: inline-block;
    position: relative
}

header nav ul li{
    color: #fff
}

header nav ul li a:hover {
    background: #444;
    color: #fff
}

header nav ul li a {
    font: 700 14px 'Open Sans';
    color: #fff;
    display: block;
    text-decoration: none!important;
    padding: 10px
}

header nav ul li .lma {
    text-align: center
}

header nav ul li a:hover {
    color: #fff
}

header nav ul li a span {
    margin-right: 10px
}

header nav ul li:hover .children {
    display: block;
}

header nav ul li .children {
    display: none;
    background: #f9f9f9;
    position: absolute;
    width: 200%;
    z-index: 1000
}

header nav ul li .caret,
header nav ul li .children li a span {
    position: relative;
    top: 3px;
    margin-left: 10px;
    margin-right: 0
}

header nav ul li .children li {
    display: block;
    overflow: hidden;
    border-bottom: 1px solid rgba(255, 255, 255, .5)
}

header nav ul li .children li a {
    display: block;
    text-align: left;
    color:#676767;
}

header nav ul li .children li a:hover{
	color:#fff;
}

@media screen and (max-width:768px) {
	.wide_container_2{margin-top: 45px!important}
    header nav,
    header nav ul li .lma {
        text-align: left
    }
    body {
        padding: 0!important
    }
    header {
        padding: 0
    }

    #header header {padding: 0px}

    header .menu_bar {
        display: block;
        /*width: 100%;*/
        width: 40px;
        position: absolute;
        top: 15px;
        left:12px;
        /*background: #3a7de3;*/
        background: #fff;
        float: none;
        /*max-width: 100%;*/
        padding: 5px 0px
    }

    header .icon-reorder {
        color: #fff
    }
    /*header .menu_bar .logo_mob {
        float: left;
        padding-left: 5px;
        padding-top: 6px;
        width: auto;
        display: block;
        font-family: 'Arial';
        font-size: 20px;
        font-weight: bold;
        color:#fff
    }*/

    header .menu_bar .logo_mob_text{float: left;padding-left: 5px;padding-top: 0px;width: auto;display: block;font-family: Arial;font-size: 20px;font-weight: 700;color: #3a7de3;width: auto}
    header .menu_bar .logo_mob{display: none!important;}
    header .menu_bar .sign_mob {
        float: right;
        padding-right: 10px;
        padding-top: 6px;
        width: auto;
        display: block;
        font-family: 'Arial';
        font-size: 15px
    }
    header .menu_bar .sign_mob a{
        font-size: 20px;
        font-weight: bold;
        color:#fff;
    }

    header .menu_bar .logo_mob img {
        width: 35px;
        padding: 3px;
        height: auto;
        padding-left:10px
    }

    header .menu_bar .ctr_menu {
        float: left;
        display: block
    }
    header .menu_bar .bt-menu {
        display: block;
        padding: 10px;
        color: #fff;
        overflow: hidden;
        font-size: 25px;
        font-weight: 700;
        text-decoration: none
    }
    header .menu_bar span {
        float: right;
        font-size: 25px
    }
    header nav {
        background: #444;
        width: 35%;
        height: calc(100% - 0px);
        position: fixed;
        right: 100%;
        margin: 0;
        overflow: scroll;
        max-width: 1000px;
        padding-top: 0;
        top: 60px
    }

    .header_home .menu_nav{
        background: #444!important
    }
    header nav ul {
        margin: 0!important;
        padding-left: 0px
    }
    header nav ul li {
        display: block;
        border-bottom: 1px solid rgba(255, 255, 255, .5)
    }
    header nav ul li a {
        color: #fff;
        display: block;
        text-decoration: none!important
    }
    header nav ul li:hover .children {
        display: none
    }
    header nav ul li .children {
        width: 100%;
        position: relative
    }
    header nav ul li .children li {
        background: #888
    }
    header nav ul li .children li a {
        text-decoration: none!important;
        margin-left: 20px
    }
    header nav ul li .caret {
        float: right
    }
}
