.ui-autocomplete{padding: 0px}
.ui-autocomplete li{list-style: none;padding-left: 0px}
.ui-autocomplete li div{padding: 10px}

.property-type-menu .content{
    margin: 5px!important;
}
.property-type-menu .ant-checkbox-group{
    margin: 5px!important;
}

.property-type-menu .ant-form-item-label{
    padding: 0px!important;
}
.ant-dropdown-trigger{
    margin: 0px!important;
}
.ant-form-inline .ant-form-item{
    margin-right: 5px!important;
    float: left;
    display: inline;
}
.no-pad.super-menu.menu {
    background: #fff;
    height: 100%;
    padding: 11px 8px;
    z-index: 0
}
.super-menu .item{
    margin: 0px!important;
    padding: 3px!important;
    display: inline-block;
    vertical-align: middle!important;
}
.property-type-menu .ant-dropdown-menu-item:hover{
    background-color: #fff!important;
}
.filter-container{
    width: 100%!important;
    background: red!important;
}

.dinline{
    display: inline-block!important;
}

.but-mobile{width: 100%;text-align: center;margin-top: 5px}

@media(max-width: 768px){
    .no-pad.super-menu.menu{
        padding-bottom: 0px
    }
}

