.searchfield{
   
    border: 0;
    border-left: 1px solid #C1C1C1;
}
.searchfield .ant-select-selection{
    border: 0px solid #d9d9d9!important;
    border-radius: 0px!important;
    height: 54px!important;
    padding: 10 0px!important; 
}
.searchfield .ant-select-selection__rendered{
    line-height: 50px!important;
}
.searchfield .ant-select-selection--single{
    cursor: text!important;
}
.searchfield .ant-select-selection__placeholder, .searchfield .ant-select-search__field__placeholder{
    color: #999!important;
}