#page-content-search{
    padding-top: 71px
}

header,
header .menu_bar .logo_mob {
    width: 100%;
    font:700 30px 'Open Sans';
}

header .menu_bar .sign_mob, header .menu_bar .logo_mob_text {
    display: none
}

#header .header_listing{
    background: #fff
}

header .logo_mob img{
    height: 50px;
    visibility: hidden;
}

header {
    padding: 10px 50px;
    box-sizing: border-box;
    background: #fff
}

header .menu_bar {
    display: inline-block;
    max-width: 200px;
    vertical-align: middle;
    z-index: 1000
}

header .ctr_menu {
    display: none
}

header nav {
    background: #fff;
    z-index: 1000;
    max-width: 1000px;
    text-align: right;
    float: right
}

header nav ul {
    list-style: none;
    margin: 0!important;
    padding-left: 0px
}

header nav ul li {
    display: inline-block;
    position: relative
}

header nav ul li{
    color: #fff
}

header nav ul li a:hover {
    background: #444;
    color: #fff
}

header nav ul li a {
    font: 700 14px 'Open Sans';
    color: #676767;
    display: block;
    text-decoration: none!important;
    padding: 10px
}

header nav ul li .lma {
    text-align: center;
    text-transform: uppercase;
}

header nav ul li a:hover {
    color: #fff
}

header nav ul li a span {
    margin-right: 10px
}

header nav ul li:hover .children {
    display: block;
}

header nav ul li .children {
    display: none;
    background: #f9f9f9;
    position: absolute;
    width: 200%;
    z-index: 1000
}

header nav ul li .caret,
header nav ul li .children li a span {
    position: relative;
    top: 3px;
    margin-left: 10px;
    margin-right: 0
}

header nav ul li .children li {
    display: block;
    overflow: hidden;
    border-bottom: 1px solid rgba(255, 255, 255, .5)
}

header nav ul li .children li a {
    display: block;
    text-align: left
}

