        left:12px;
        /*background: #3a7de3;*/
        background: #fff;
        float: none;
        /*max-width: 100%;*/
        padding: 5px 0px
    }
    header .icon-reorder {
        color: #fff
    }
    /*header .menu_bar .logo_mob {
