    header .menu_bar .logo_mob_text{float: left;padding-left: 5px;padding-top: 0px;width: auto;display: block;font-family: Arial;font-size: 20px;font-weight: 700;color: #3a7de3;width: auto}
    header .menu_bar .logo_mob{display: none!important;}
    header .menu_bar .sign_mob {
