    header .menu_bar span {
        float: right;
        font-size: 25px
    }
    header nav {
        background: #444;
        width: 30%;
        height: calc(100% - 0px);
        position: fixed;
        right: 100%;
        margin: 0;
        overflow: scroll;
        max-width: 1000px;
        padding-top: 0;
        top: 60px
    }
    header nav ul {
        margin: 0!important;
        padding-left: 0px
    }
    header nav ul li {
        display: block;
        border-bottom: 1px solid rgba(255, 255, 255, .5)
    }
    header nav ul li a {
        color: #fff;
        display: block;
        text-decoration: none!important
    }
    header nav ul li:hover .children {
        display: none
    }
    header nav ul li .children {
        width: 100%;
        position: relative
    }
    header nav ul li .children li {
        background: #888
    }
    header nav ul li .children li a {
        text-decoration: none!important;
        margin-left: 20px
    }
    header nav ul li .caret {
        float: right
    }
}

