.form-register{
    max-width: 300px;
    padding: 10px!important;

    background: #f5f5f5;
    border: 1px solid #ccc;
    border-radius: 8px;
    
    margin: 0px auto!important;
    margin-top: calc(50%)!important;
}
.form-register .item-form{
    width: 100%;
}
.form-register .item-form .ant-row.ant-form-item{
    margin: 0px!important;
}
.form-register .register-form-forgot {
    float: right;
}
.form-register  .register-form-button {
    width: 100%;
}

.form-register .footer-advanced-form .ant-row{
    margin: 5px;
}