.demo-infinite-container {
  overflow-y: auto;
  overflow-x: hidden;
  padding: 8px 24px;
  /* max-height: 680px; */
  display: block;
  margin-top: -8px;
  /* background: red; */
  /* height: 100vh; */
  position: relative;
  padding-bottom: 40px;
}

  .demo-loading-container {
    width: 100%;
    text-align: center;
    height: 30px;
    /* background: #3a7de3; */
    padding: 10px;
    display: block;
}

  .demo-infinite-container .ant-card-cover {
	height: 200px!important;
	overflow: hidden;
	background-size: 100% 100%;
}
.demo-infinite-container  .ant-card-cover img {
    border-radius: 2px 2px 0 0;
    height: 100%;
    width: 100%;
}
.demo-infinite-container  .ant-card-cover:hover::after {
    position: absolute;
    content: ' ';
    width: 100%;
    height: 200px;
    top: 0px;
    left: 0px;
    background: rgba(0, 0, 0, 0.4);
}
.demo-infinite-container .wide-2 .ffs-bs {
	display: none;
}
.demo-infinite-container .ant-card-cover:hover .wide-2 .ffs-bs {
    position: absolute;
    z-index: 1;
    display: block;
    text-align: center;
    margin: 0px auto;
    align-items: center;
    display: flex;
    justify-items: center;
    top: 0px;
	left: calc(50% - 80px);
	display: block;
}

.demo-infinite-container  .ant-card-body {
    padding: 10px!important;
    height: 110px!important;
	font-size: 12px;
}
.wide-2 .white{
	width: 100%!important;
}
.footer-list{
  text-align: center;
}

